import React, { useEffect, useState } from "react";
import axios from '../../api/apiConfiger'
import "./main.scss";


const Main = () => {
  const [main_IMG, setmain_IMG] = useState('')

useEffect(() => {
  axios.get('natural').then((responce) => {
    setmain_IMG( responce.data.hits[Math.floor(Math.random()*20)].largeImageURL)
    console.log();
  })
}, [])

  

console.log();




  return (
    <>
      
      <div className=" maim-img-bg">
        <img
          // src={require("../../image/mainimg.png")}
          src={main_IMG}
          alt="mainimage"
          srcset=""
        />
          <img className="overlay-img"
          src={require("../../image/mainImgoverly.png")}
          alt="mainimage-overlay"
          srcset=""
        />

        <div className="main-info-container">
          <div className="info-wraper">
            <p>
              The new way to <br /> plan your next <br /> adventure
            </p>
            <span>Explore the colourful world</span>
            <button className="button"> </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Main;
